/* Reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #1a1a1a; /* Dark background color */
  color: #fff; /* Text color */
}

/* Container Styles */
.container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #333; /* Container background */
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.1);
}

/* Header Styles */
header {
  text-align: center;
  margin-bottom: 20px;
}

h1 {
  font-size: 2.5em;
  color: #03a9f4; /* Header color */
}

/* Content Styles */
.content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

/* Section Styles */
section {
  flex: 1;
  margin-bottom: 20px;
  padding: 20px;
  border: 1px solid #444; /* Border around section */
  border-radius: 10px;
  background-color: #2c2c2c; /* Dark section background */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* Increased shadow intensity */
  transition: background-color 0.3s ease-in-out; /* Transition effect */
}

section:hover {
  background-color: #383838; /* Darker background on hover */
}

h2 {
  font-size: 2em;
  color: #03a9f4; /* Section title color */
  margin-bottom: 20px;
}

/* Roll Results Table Styles */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

th, td {
  border: 1px solid #555; /* Darker border color */
  padding: 10px;
  text-align: left;
}

/* Controls Styles */
.controls {
  flex-basis: calc(30% - 20px);
}

.input-container {
  margin-bottom: 20px;
}

label {
  display: block;
  font-size: 1.2em;
  margin-bottom: 5px;
}

input {
  padding: 10px;
  font-size: 1em;
  width: 100%;
  border: 1px solid #555; /* Darker border color */
  border-radius: 5px;
  margin-bottom: 10px;
}

.button-container {
  display: flex;
  justify-content: space-between;
}

button {
  padding: 10px 20px;
  font-size: 1.2em;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #03a9f4; /* Button color */
  color: #fff;
  transition: background-color 0.3s ease-in-out; /* Transition effect */
}

button:hover {
  background-color: #0277bd; /* Darker blue on hover */
}

/* Number Tally Styles */
.number-tally {
  flex-basis: calc(40% - 20px);
}

.tally-items {
  display: flex;
  flex-wrap: wrap;
}

.number-tally-item {
  flex-basis: calc(33.33% - 10px);
  padding: 10px;
  border: 1px solid #555; /* Darker border color */
  border-radius: 5px;
  margin-bottom: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* Increased shadow intensity */
  transition: background-color 0.3s ease-in-out; /* Transition effect */
}

.number-tally-item:hover {
  background-color: #383838; /* Darker background on hover */
}

.number-tally-item h3 {
  font-size: 1.5em;
  color: #03a9f4; /* Title color */
  margin-bottom: 5px;
}

.number-tally-item p {
  font-size: 1.2em;
  line-height: 1.5;
  color: #fff; /* Text color */
}

/* Additional Tally Styles */
.additional-tally {
  display: flex;
  justify-content: space-between;
}

/* Responsive Styles */
@media (max-width: 600px) {
  .content {
    flex-direction: column;
  }

  section {
    flex: 1 100%;
  }

  .controls, .number-tally {
    flex-basis: calc(100% - 20px);
  }
}
